import countries from '~global/variables/countries.json';

export const phoneCodes = [];

countries.list.forEach((country) => {
  if (country.phoneCode) {
    phoneCodes.push({
      code: country.phoneCode,
      iso2Code: country.iso2Code,
      country: country.name,
      slug: country.slug,
      label: `forms.country.option.${country.iso2Code}`,
    });
  }
});

export const defaultTelephoneCode = '+44';

export const localeToTelephoneCodeMapping = {
  'en-gb': '+44',
  'en-us': '+1',
  'zh-cn': '+86',
  'zh-tw': '+886',
  'zh-hk': '+852',
  'ko-kr': '+82',
  'ja-jp': '+81',
  'th-th': '+66',
  'vi-vn': '+84',
  'de-de': '+49',
  'el-gr': '+30',
  'el-cy': '+357',
  'es-es': '+34',
  'es-la': '+52',
  'fr-fr': '+33',
  'it-it': '+39',
  'pt-br': '+55',
  'ru-ru': '+7',
  'tr-tr': '+90',
};

export function getDefaultCountryCode(locale) {
  if (localeToTelephoneCodeMapping[locale]) {
    return localeToTelephoneCodeMapping[locale];
  }

  return defaultTelephoneCode;
}

export function getFormattedPhoneNumber(countryCode, number) {
  return `(${countryCode}) ${number}`;
}

/**
 * Takes a formatted number and splits it into country code and number
 *
 * @param  {String} number e.g. "(+1) 012 345 6789" or "(+44) 07123456789"
 *
 * @return {Object}
 */
export function getPhoneNumberParts(number) {
  const splitNumber = {
    countryCode: null,
    phoneNumber: null,
  };

  try {
    const numberParts = number.split('(')[1].split(')');
    // trim leading/trailing whitespace from telephone number
    numberParts[1] = numberParts[1].trim();
    splitNumber.countryCode = numberParts[0];
    splitNumber.phoneNumber = numberParts[1];
  } catch (error) {
    // can't have empty catch block
  }

  return splitNumber;
}



